<template>
  <div>
    <v-btn 
    v-for="(link, key) in validLinks"
    :key="key"
    :color="color"
    plain 
    fab
    small 
    :href="link"
    target="_blank"
    >
      <v-icon>{{ icons[key] }}</v-icon>
    </v-btn>
  </div>
</template>

<script>
import { 
  mdiWeb, 
  mdiTwitter, 
  mdiInstagram, 
  mdiEmailOutline,
  mdiFacebook
} from '@mdi/js'

export default {
  props: {
    links: {
      type: Object,
      default: () => ({})
    },
    color: {
      type: String,
      default: "red"
    }
  },
  data() {
    return {
      icons: {
        'website': mdiWeb,
        'twitter': mdiTwitter,
        'instagram': mdiInstagram,
        'email': mdiEmailOutline,
        'facebook': mdiFacebook
      }
    }
  },
  methods: {
    isValid(url) {
      return (url && url.trim() != '') 
    }
  },
  computed: {
    validLinks() {
      return Object.keys(this.links)
        .filter(key => this.isValid(this.links[key]))
        .reduce((result, key) => (result[key] = this.links[key], result), {});
    }
  }
}
</script>