<template>
  <div>
    <v-list-item
    @click="selected"
    >
      <v-list-item-avatar
        :size="$vuetify.breakpoint.xs ? 40 : 55"
      >
        <v-img
        :src="appData.icon"
        ></v-img>
      </v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title>{{ appData.name }}</v-list-item-title>
      </v-list-item-content>
      <v-list-item-action>
        <v-btn
        rounded
        outlined
        :color="appData.color"
        :small="$vuetify.breakpoint.xs"
        >
          {{ ctaTextCustomised }}
        </v-btn>
      </v-list-item-action>
    </v-list-item>
    <v-divider
    v-if="divider"
    ></v-divider>
  </div>
</template>

<script>
import Utils from '../PodcastRedirectWidget/utils.js'

export default {
  props: {
    appData: {
      type: Object,
      default: () => ({})
    },
    ctaText: {
      type: String,
      default: "Subscribe"
    },
    divider: {
      type: Boolean,
      default: true
    },
    action: String
  },
  methods: {
    selected() {
      this.$emit('click', this.appData.id);
    }
  },
  computed: {
    ctaTextCustomised() {
      if (Utils.hasProperty(this.appData.actions[this.action], 'ctaOverride')) {
        return this.appData.actions[this.action].ctaOverride;
      } else {
        return this.ctaText;
      }
    }
  }
}
</script>