import Config from './config'

const subscribe = Config.actions.subscribe;
// const rate = Config.actions.rate;

export default {
  // Multi-platform desktop apps
  spotifyDesktop: (platform) => {
    return platform.add('Spotify', 'spotify-desktop', 'spotify')
      .setTheme('green')
      .addAction(subscribe)
  },
  podchaser: (platform) => {
    return platform.add('Podchaser')
      .setTheme('green')
      .addAction(subscribe)
  },

  // Multi-platform mobile apps
  spotifyMobile: (platform) => {
    return platform.add('Spotify', 'spotify-mobile', 'spotify')
      .setTheme('green')
      .addAction(subscribe)
  },
  acast: (platform) => {
    return platform.add('Acast')
      .setTheme('black')
      .addAction(subscribe)
  },
  stitcher: (platform) => {
    return platform.add('Stitcher')
      .setTheme('blue')
      .addAction(subscribe)
  },
  pocketCasts: (platform) => {
    return platform.add('Pocket Casts')
      .setTheme('red accent-2')
      .addAction(subscribe)
  },
  tuneIn: (platform) => {
    return platform.add('TuneIn')
      .setTheme('teal')
      .addAction(subscribe)
  },
  podbean: (platform) => {
    return platform.add('Podbean')
      .setTheme('green darken-1')
      .addAction(subscribe)
  },
  iheartRadio: (platform) => {
    return platform.add('iHeart Radio')
      .setTheme('red')
      .addAction(subscribe)
  },
  deezer: (platform) => {
    return platform.add('Deezer')
      .setTheme('black')
      .addAction(subscribe)
  },
}