import PlatformData from '../PlatformData'
import Mixins from '../Mixins'
import Config from '../config'

const platform = new PlatformData();

platform.add('Apple Podcasts', 'apple-podcasts-mobile', 'apple-podcasts')
  .setTheme('purple')
  .addAction(Config.actions.subscribe)

Mixins.spotifyMobile(platform);
Mixins.acast(platform);
Mixins.stitcher(platform);
Mixins.pocketCasts(platform);
Mixins.tuneIn(platform);
Mixins.podbean(platform);
Mixins.deezer(platform);

export default platform