import Utils from './utils'
import Config from './config'

export default class AppData {
  constructor(initial = {}) {
    this.data = initial;
    return this;
  }

  get(action, urls) {
    const replaceWith = { ...this.data };

    // Add the podcast's custom urls to the data object
    Object.keys(urls).forEach((key) => {
      // Create the sub object if it doesn't exist
      if (Utils.hasProperty(replaceWith, key) === false) {
        replaceWith[key] = {}
      } 
      
      replaceWith[key].url = urls[key]
    })

    // Remove any apps without the specified action
    // Remove any apps without a url set
    let inflated = { ...this.data };
    Object.keys(inflated).forEach((key) => {
      if (Utils.hasProperty(inflated[key].actions, action) === false) {
        // Doesn't have action, remove the app
        delete inflated[key]
      } else if (Utils.hasProperty(inflated[key], 'url') === false) {
        // No URL was provided, remove the app
        delete inflated[key]
      } 
    })

    // Traverse over data & replace any template tags in text fields
    Utils.deepFindReplaceTemplateTags(inflated, replaceWith)
    return inflated;
  }

  add(name, customId = '', customIconId = '') {
    let id = customId;
    if (id === '') {
      id = name.toLowerCase().replace(' ', '-');
    }

    let iconId = customIconId;
    if (iconId === '') {
      iconId = id;
    }
    const icon = Utils.makeIconUrl(iconId);
    if (!icon) {
      console.warn(`AppData: Could not find an icon for '${iconId}'`)
    }

    const newData = {
      id,
      name,
      icon,
      addAction: (name, ctaOverride) => this.addAction(id, name, ctaOverride),
      actions: {
        
      },
      setTheme: (color) => {
        newData.color = color
        return newData
      }
    }

    this.data[id] = newData;

    return newData;
  }

  addAction(id, name, ctaOverride = '') {
    if (Config.actionsAllowlist.includes(name) === false) {
      console.error(`PlatformData: addAction: '${name}' is not a valid action name, must be one of: ${Config.actionsAllowlist}`)
      return this.data[id];
    }

    const action = {
      instructions: [],
      addStep: (message) => this.addInstructionStep(id, name, message),
      addAction: (newName) => this.addAction(id, newName)
    }

    if (ctaOverride.length > 0) {
      action.ctaOverride = ctaOverride;
    }

    this.data[id].actions[name] = action;

    return action;
  }

  addInstructionStep(id, actionName, message) {
    const step = {
      message
    }

    step.withLink = (url, title = "Open") => {
      step.link = {
        url,
        title
      }
      return step;
    }

    step.withImage = (url) => {
      step.image = {
        url
      }
      return step;
    }

    step.addStep = (message) => this.addInstructionStep(id, actionName, message)
    step.addAction = (name, ctaOverride) => this.addAction(id, name, ctaOverride)

    this.data[id].actions[actionName].instructions.push(step)
    return step;
  }

  clone() {
    return new AppData({
      ...this.data
    });
  }

  
}