import Vue from 'vue'
import PodcastRedirectWrapper from './components/PodcastRedirectWrapper.vue'
import vuetify from './plugins/vuetify';
import Config from './PodcastRedirectWidget/config'

Vue.config.productionTip = false

if (process.env.NODE_ENV === 'production') {
  // Script triggered mount
  window.PodcastRedirectWidget = (
    urls = null,
    theme = {},
    socialLinks = {},
    primaryApps = Config.primaryAppsDefault,
    elName = '#podcast-widget'
    ) => {
    new Vue({
      vuetify,
      render: h => h(PodcastRedirectWrapper, {
        props: {
          urls,
          theme,
          socialLinks,
          primaryApps
        }
      })
    }).$mount(elName)
  }
} else {
  new Vue({
    vuetify,
    render: h => h(PodcastRedirectWrapper)
  }).$mount('#app')
}
