<template>
  <v-app class="overflow-fix">
    <div class="podcast-widget-wrapper">
      <div
      v-if="themeInt.embedded === true"
      :class="[{'mt-10': themeInt.allowActionSwitching}]"
      >
        <PodcastRedirectWidget
        v-if="themeInt.embedded"
        :urls="urls"
        :primaryApps="primaryApps"
        :action="actionInt"
        :socialLinks="socialLinks"
        :theme="themeInt"
        :embedded="true"
        ></PodcastRedirectWidget>

        <ActionSwitch
        v-if="themeInt.allowActionSwitching"
        :action="actionInt"
        :theme="themeInt"
        @click="toggleAction"
        :embedded="true"
        ></ActionSwitch>
      </div>

      <v-container 
      v-if="themeInt.embedded === false"
      fluid 
      fill-height
      class="background-wrapper pt-12 pt-md-0"
      >
        <div 
        :class="['background', {'image': backgroundIsImage}]"
        :style="backgroundStyle"
        ></div>
        <div
        class="background tint"
        :style="tintStyle"
        ></div>
        <v-row
        justify="center"
        >
          <v-col 
          cols="12" 
          sm="9" 
          md="5" 
          lg="4" 
          class="content"
          >
            <PodcastRedirectWidget
            :urls="urls"
            :primaryApps="primaryApps"
            :action="actionInt"
            :socialLinks="socialLinks"
            :theme="themeInt"
            @app-choice="(choice) => $emit('app-choice', choice)"
            ></PodcastRedirectWidget>

            <ActionSwitch
            v-if="themeInt.allowActionSwitching"
            :action="actionInt"
            :theme="themeInt"
            @click="toggleAction"
            ></ActionSwitch>

            <CtaCallout
            v-if="themeInt.showCallout"
            :message="`${actionNiceName}!`"
            ></CtaCallout>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import PodcastRedirectWidget from './PodcastRedirectWidget'
import CtaCallout from './CtaCallout'
import ActionSwitch from './ActionSwitch'
import Utils from '../PodcastRedirectWidget/utils'
import Config from '../PodcastRedirectWidget/config'

export default {
  components: {
    PodcastRedirectWidget,
    CtaCallout,
    ActionSwitch
  },
  props: {
    action: {
      type: String,
      default: 'subscribe'
    },
    theme: {
      type: Object,
      default: () => ({})
    },
    urls: {
      type: Object,
      default: () => ({})
    },
    primaryApps: {
      type: Array,
      default: () => ([...Config.primaryAppsDefault])
    },
    socialLinks: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      actionInt: ''
    }
  },
  methods: {
    toggleAction() {
      if (this.actionInt === Config.actions.subscribe) {
        this.actionInt = Config.actions.rate
      } else {
        this.actionInt = Config.actions.subscribe
      }
    }
  },
  computed: {
    themeInt() {
      return {
        ...Config.themeDefault,
        ...this.theme
      };
    },

    backgroundStyle() {
      if (this.themeInt.background === '') return '';
      if (this.backgroundIsImage) {
        return `background-image: url('${this.themeInt.background}')`
      } else {
        return `background-color: ${this.themeInt.background}`
      }
    },

    tintStyle() {
      if (this.tintColor === '') return '';
      return `background-color: ${this.themeInt.backgroundTintColor}; opacity: ${this.themeInt.backgroundTintOpacity};`
    },

    backgroundIsImage() {
      return this.themeInt.background.includes('/')
    },

    actionNiceName() {
      return Utils.actionNiceName(this.themeInt, this.action)
    },
  },
  watch: {
    action(newVal) {
      this.actionInt = newVal;
    },
    actionInt(newVal) {
      this.$emit('action-changed', newVal)
    } 
  },
  mounted() {
    this.actionInt = this.action;
    Utils.checkUserProps(
      this.urls,
      this.socialLinks,
      this.theme,
      this.primaryApps,
      this.action
    )
  },
}
</script>

<style lang="scss">

  .overflow-fix {
    overflow-x: hidden;
  }

  .podcast-widget-wrapper {
    width: 100%;
    height: 100%;

    .background-wrapper {
      background-color: #777;
      
      .content {
        z-index: 2;
        position: relative;
        max-width: 33rem;
      }

      .background {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;

        .tint {
          z-index: 1;
        }

        &.image {
          z-index: 0;
          background-size: cover;
          filter: blur(12px);
        }
      }  
    }
  }


</style>