const platformLookup = {
  'windows': ["Windows", "Windows Server 2008 R2 / 7", "Windows Server 2008 / Vista", "Windows XP"],
  'osx': ["OS X"],
  'android': ["Android"],
  'ios': ["iOS"],
  'windows-phone': ["Windows Phone"],
  'linux': ["Linux", "Ubuntu", "Debian", "Fedora", "Red Hat", "SuSE"]
}

const allPlatformData = {
  android,
  ios,
  auto: ios
}

// Detect what platform this browser is on, show the relevant apps
// if they are available (show default if not)
const currentPlatformId = platformLookup[Platform.os.family]
if (Utils.hasProperty(allPlatformData, currentPlatformId)) {
  // We have custom data for this device type, use it...
  allPlatformData.auto = allPlatformData[currentPlatformId]
} else {
  console.warn(`PodcastRedirectWidget: Unrecognised platform family '${Platform.os.family}'`)
}

export default allPlatformData