<template>
  <div 
  :class="['mode-switch-wrapper', { 'compact': $vuetify.breakpoint.smAndDown || theme.switchAlwaysCompactStyle, 'embedded': embedded === true, 'isSmAndDown': $vuetify.breakpoint.smAndDown}]"
  >
    <div
    class="title question mb-3 d-none d-sm-block"
    :style="fontStyle"
    >
      {{ this.titleText[this.action] }}
    </div>
    <v-btn
    class="button"
    rounded
    dark
    :color="theme.switchButtonColor"
    :large="$vuetify.breakpoint.smAndDown === false"
    @click="$emit('click')"
    >
      {{ this.buttonText[this.action] }}
    </v-btn>
  </div>
</template>

<script>
export default {
  props: ['action', 'theme', 'embedded'],
  computed: {
    fontStyle() {
      return `color: ${this.theme.switchTitleColor};`
    },
    
    titleText() {
      return {
        rate: this.theme.switchTitleTextRate,
        subscribe: this.theme.switchTitleTextSubscribe
      }
    },

    buttonText() {
      return {
        rate: this.theme.switchButtonTextRate,
        subscribe: this.theme.switchButtonTextSubscribe
      }
    }
  }
}
</script>

