<template>
  <v-list
  two-line
  class="py-0"
  >
    <v-list-item-group>
      <AppListItem
      v-for="(key, index) in order"
      :key="key"
      :appData="allAppData[key]"
      :divider="index < ( appCount - 1) || hasSibling === true"
      :ctaText="ctaText"
      :action="action"
      @click="(id) => $emit('click', id)"
      ></AppListItem> 
    </v-list-item-group>
  </v-list>
</template>

<script>
import AppListItem from './AppListItem'

export default {
  components: {
    AppListItem
  },
  props: {
    allAppData: {
      type: Object,
      default: () => ({})
    },
    order: {
      type: Array,
      default: () => ([])
    },
    ctaText: {
      type: String,
      default: "Subscribe"
    },
    hasSibling: {
      type: Boolean,
      default: false
    },
    action: String
  },
  computed: {
    appCount() {
      return this.order.length;
    }
  }
}
</script>