<template>
  <div>
    <v-img
      v-if="hasImage"
      :src="this.step.image.url"
      height="150"
      ></v-img>

    <v-list-item
    @click="selected"
    >
      <v-list-item-avatar
        size="30"
      >
        <v-chip>
          {{ index }}
        </v-chip>
      </v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title>{{ step.message }}</v-list-item-title>
      </v-list-item-content>
      <v-list-item-action
      v-if="hasLink"
      >
        <v-btn
        rounded
        outlined
        >
          {{ step.link.title }}
        </v-btn>
      </v-list-item-action>
    </v-list-item>
    <v-divider></v-divider>
  </div>
</template>

<script>
import Utils from '../PodcastRedirectWidget/utils.js'

export default {
  props: ['step', 'index'],
  computed: {
    hasImage() {
      return Utils.hasProperty(this.step, 'image');
    },

    hasLink() {
      return Utils.hasProperty(this.step, 'link');
    }
  },
  methods: {
    selected() {
      if (this.hasLink) {
        window.open(this.step.link.title, "_blank")
      }
    }
  },
}
</script>