<template>
  <v-card
  :class="['widget-wrapper', {'embedded': embedded === true}]"
  >
    <v-img 
    v-if="theme.showHeader === true"
    :height="theme.headerHeight"
    :src="theme.headerBackgroundImage"
    class="align-end"
    :position="theme.headerBackgroundImagePosition"
    :style="`background-color: ${theme.headerBackgroundColor};`"
    :gradient="theme.headerBackgroundGradient === true ? 'to bottom, rgba(0,0,0,.1), rgba(0,0,0,.7)' : null"
    >
      <v-spacer></v-spacer>
      <v-card-title
      :style="`color: ${theme.headerTitleColor}`"
      >{{ theme.title }}</v-card-title>
      <v-card-subtitle 
      v-if="haveSubtitle"
      :style="`color: ${theme.headerSubtitleColor}`"
      >
        {{ theme.subtitle }}
      </v-card-subtitle>
      <SocialLinks
      class="top-right text-right ma-1 pa-0"
      :links="socialLinks"
      :color="theme.headerSocialButtonColor"
      ></SocialLinks>
    </v-img>

    <AppList
    v-if="haveSelectedApp === false"
    :allAppData="allAppData"
    :order="appOrder.primary"
    :ctaText="actionNiceName"
    :hasSibling="appOrder.secondary.length > 0"
    :action="action"
    @click="selectApp"
    ></AppList>

    <AppIconList
    v-if="haveSelectedApp === false && appOrder.secondary.length > 0 && theme.showSecondaryApps"
    :allAppData="allAppData"
    :order="appOrder.secondary"
    :actionNiceName="actionNiceName"
    @click="selectApp"
    ></AppIconList>

    <InstructionList
    v-if="haveSelectedApp"
    :appData="selectedApp"
    :action="action"
    @close="deselectApp"
    ></InstructionList>

    <PlatformSelect
    v-if="false"
    ></PlatformSelect>
  </v-card>
</template>

<script>
import PodcastRedirect from '../PodcastRedirectWidget'
import AppList from './AppList'
import InstructionList from './InstructionList'
import Utils from '../PodcastRedirectWidget/utils.js'
import PlatformSelect from './PlatformSelect'
import AppIconList from './AppIconList'
import SocialLinks from './SocialLinks'

export default {
  components: {
    AppList,
    InstructionList, 
    PlatformSelect,
    AppIconList,
    SocialLinks
  },
  props: [
    'title', 
    'subtitle', 
    'theme', 
    'urls', 
    'primaryApps', 
    'action', 
    'socialLinks',
    'inlineActionSwitch',
    'embedded'
  ],
  data() {
    return {
      selectedAppId: '',
      selectedPlatform: 'auto',
    }
  },
  computed: {
    // Filter the available apps by the priority settings
    appOrder() {
      const allKeys = Object.keys(this.allAppData);

      if (this.theme.numPrimaryApps === -1) {
        return {
          primary: allKeys,
          secondary: []
        };
      } else if (this.theme.numPrimaryApps === 0) {
        return {
          primary: [],
          secondary: allKeys
        }
      }

      // Find the primary apps in order
      let primary = [];
      this.primaryApps.forEach((key) => {
        if (Utils.hasProperty(this.allAppData, key)) {
          primary.push(key);
        }
      })

      // Limit the length of the list & keep the discards
      let secondary = []
      if (primary.length > this.theme.numPrimaryApps) {
        secondary = primary.splice(this.theme.numPrimaryApps, (primary.length - this.theme.numPrimaryApps))
      }

      // Find the secondary apps
      allKeys.forEach((key) => {
        if (this.primaryApps.includes(key) === false) {
          secondary.push(key);
        }
      }) 

      // Top up the primary list if it isn't at capacity
      if (primary.length < this.theme.numPrimaryApps) {
        primary = [
          ...primary,
          ...secondary.splice(
            0,
            Math.min(secondary.length, this.theme.numPrimaryApps - primary.length) 
          )
        ]
      }

      return {
        primary: primary,
        secondary: secondary
      };
    },

    actionNiceName() {
      return Utils.actionNiceName(this.theme, this.action)
    },

    allAppData() {
      return PodcastRedirect[this.selectedPlatform].get(this.action, this.urls);
    },

    haveSubtitle() {
      return this.theme.subtitle.trim().length > 0;
    }, 

    haveSelectedApp() {
      if (this.selectedAppId === '') return false;
      return Utils.hasProperty(this.allAppData, this.selectedAppId)
    },
    
    selectedApp() {
      return this.allAppData[this.selectedAppId];
    },
  },
  methods: {
    selectApp(id) {
      // If selected app has instructions, show them...
      const app = this.allAppData[id];
      if (app.actions[this.action].instructions.length > 0) {
        this.selectedAppId = id;
      } else {
        // Doesn't have any instructions, so just open the url directly
        // We should still be within the trusted event context tree here
        // so this shouldn't cause a popup block warning
        window.open(app.url, "_blank")
      }

      this.$emit('app-choice', id)
    },
    deselectApp() {
      this.selectedAppId = '';
    }
  },
  mounted() {
    // console.log(PodcastRedirect);
  }
}
</script>

<style lang="scss">
  .widget-wrapper {
    &.embedded {
      z-index: 1;
    }

    .top-right {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
</style>