<template>
  <div>
    <v-list-item>
      <v-list-item-action>
        <v-btn 
        @click="$emit('close')"
        rounded 
        fab 
        small
        outlined
        color="grey"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </v-list-item-action>
      <v-list-item-content class="text-right">
        <v-list-item-title>{{ appData.name }}</v-list-item-title>
        <v-list-item-subtitle> Follow these steps to {{ this.action }}</v-list-item-subtitle>
      </v-list-item-content>
      <v-list-item-avatar
        size="55"
      >
        <v-img
        :src="appData.icon"
        ></v-img>
      </v-list-item-avatar>
    </v-list-item>
    <v-divider></v-divider>
    <v-list class="py-0">
      <InstructionListStep
        v-for="(step, index) in instructions"
        :key="index"
        :step="step"
        :index="index + 1"
      ></InstructionListStep> 
    </v-list>
  </div>
</template>

<script>
import InstructionListStep from './InstructionListStep'

export default {
  components: {
    InstructionListStep
  },
  props: ['appData', 'action'],
  computed: {
    instructions() {
      return this.appData.actions[this.action].instructions
    }
  }
