import Config from './config'

export default class Utils {

  static hasProperty(target, propertyName) {
    return Object.prototype.hasOwnProperty.call(target, propertyName)
  }

  static isObject(value) {
    return typeof value === 'object' && value !== null;
  }

  static isString(value) {
    return typeof value === 'string' || value instanceof String;
  }

  static isArray(value) {
    return Array.isArray(value);
  }

  static actionNiceName(theme, action) {
    return theme[`${action}Text`];
  }

  static makeIconUrl(id) {
    return `${Config.iconUrl}/${id}.png`
  }

  static warn(message) {
    console.warn(`${Config.appName}: ${message}`)
  }

  static guardArray(target, name) {
    if (this.isArray(target)) {
      return true;
    } else {
      this.warn(`'${name}' is required & must be an array`)
      return false;
    }
  }

  static guardObject(target, name) {
    if (this.isObject(target)) {
      return true;
    } else {
      this.warn(`'${name}' is required & must be an object`)
      return false;
    }
  }

  static guardEmptyObject(target, name) {
    if (this.guardObject(target, name)) {
      if (Object.keys(target).length === 0) {
        this.warn(`'${name}' object must have at least one value`)
        return false;
      } else {
        return true;
      }
    } else {
      return false;
    }
  }

  static guardArrayAllowlist(target, name, allowlist, descriptor = 'values') {
    const unknownKeys = [];
    target.forEach(key => {
      if (allowlist.includes(key) === false) {
        unknownKeys.push(key)
      }
    })

    if (unknownKeys.length > 0) {
      this.warn(`'${name}' contained one or more invalid ${descriptor}: ${unknownKeys}`)
      return false;
    }
    return true;
  }

  static guardObjectKeyAllowlist(target, name, allowlist) {
    return this.guardArrayAllowlist(
      Object.keys(target),
      name,
      allowlist,
      'properties'
    )
  }

  static checkUserProps(urls, socialLinks, theme, primaryApps, action) {
    return (
      this.guardEmptyObject(urls, 'urls') &&
      this.guardObjectKeyAllowlist(urls, 'urls', Config.appsAllowlist) &&
      this.guardObject(socialLinks, 'socialLinks') &&
      this.guardObjectKeyAllowlist(socialLinks, 'socialLinks', Config.socialLinksAllowlist),
      this.guardObject(theme, 'theme') &&
      this.guardObjectKeyAllowlist(theme, 'theme', Config.themeAllowlist) &&
      this.guardArray(primaryApps, 'primaryApps') &&
      this.guardArrayAllowlist(primaryApps, 'primaryApps', Config.appsAllowlist) &&
      this.guardArrayAllowlist([action], 'action', Config.actionsAllowlist)
    )
  }

  static deepFindReplaceTemplateTags(anchor, replaceWithObject, maxDepth = 10) {
    if (maxDepth === 0) {
      console.warn("Utils: deepFindReplaceTemplateTags: Reached maximum depth, aborting without applying template.")
      return anchor;
    }

    Object.keys(anchor).forEach((key) => {
      if (this.isObject(anchor[key])) {
        // Keep going down the tree
        anchor[key] = this.deepFindReplaceTemplateTags(anchor[key], replaceWithObject, (maxDepth - 1))
      } else if (this.isString(anchor[key])) {
        // Can do replacement
        anchor[key] = this.findReplaceTemplateTags(anchor[key], replaceWithObject)
      } else {
        // Can't do replacement, leave it as is
      }
    })

    return anchor;
  }

  // Find object descriptions with the format {{firstname.secondname.thirdname.etc}} in the given object
  static findReplaceTemplateTags (toCheck, replaceWithObject) {
    const regex = /{{ ?(?:([A-Za-z0-9-_.]+))+ ?}}/g;
    let match;
    let replaced = toCheck;

    while ((match = regex.exec(toCheck)) !== null) {
        // This is necessary to avoid infinite loops with zero-width matches
        if (match.index === regex.lastIndex) regex.lastIndex++;
        if (match.length > 0) {
          const toReplace = match[0];
          const path = match[1];
          const replaceWith = this.getValueFromObjectPath(replaceWithObject, path);
          if (replaceWith !== undefined) {
            replaced = replaced.replace(toReplace, replaceWith);
          }
        }
    }

    return replaced;
  }

  // Traverse a given root object to find the value at the given path node, or return undefined
  // e.g. firstname.secondname.thirdname.etc
  static getValueFromObjectPath(root, pathStr, skipFirst = false) {
    const path = pathStr.trim().split('.');
    let head = root;
    let fail = false;
    path.forEach((level, index) => {
      if (fail) return;
      if (index === 0 && skipFirst) return;
      if (head[level] === undefined) fail = true;
      head = head[level];
    })
    return (fail) ? undefined : head;
  }
}