  <div class="callout-wrapper d-none d-md-block">
    <div 
    class="text"
    :style="colorStyle"
    >
      <span> {{ message }} </span>
    </div>
    <SvgArrow 
    :flip="true" 
    :color="color" 
    size="200px"
    ></SvgArrow>
  </div>
</template>

<script>
import SvgArrow from './InlineSVGs/Arrow.vue'

export default {
  components: {
    SvgArrow
  },
  props: {
    color: {
      type: String,
      default: 'white'
    },
    message: {
      type: String,
      default: ''
    }
  },
  computed: {
    colorStyle() {
      return `color: ${this.color}`
    }
  }
}
</script>

